/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import { useEffect, useState } from "react"
import CommandeService from "../../../../services/commandeService"
import formatDate from "../../../../utils/date-format"
import formatPrice from "../../../../utils/formatPrice"
import { useAuthContext } from "../../../../contexts/useAuthContext"
import { CheckCircle, Truck, Clock, ChevronDown, ChevronUp } from "lucide-react"
import { toast } from "react-toastify"

export default function CommandesVenteCher() {
  const [commandes, setCommandes] = useState([])
  const [loading, setLoading] = useState(true)
  const [currentPage, setCurrentPage] = useState(1)
  const [ordersPerPage] = useState(10)
  const [expandedRows, setExpandedRows] = useState(new Set())
  const { user } = useAuthContext()

  useEffect(() => {
    const fetchModels = async () => {
      setLoading(true)
      try {
        const data = await CommandeService.getCommandesVenteCher(user.id)
        setCommandes(Array.isArray(data) ? data : [])
      } catch (error) {
        // eslint-disable-next-line no-console
        console.error("Erreur lors de la récupération des commandes en cher", error)
        toast.error("Erreur lors de la récupération des commandes en cher", {
          position: "top-center",
        })
      } finally {
        setLoading(false)
      }
    }
    if (user && user.id) {
      fetchModels()
    }
  }, [user])

  const indexOfLastOrder = currentPage * ordersPerPage
  const indexOfFirstOrder = indexOfLastOrder - ordersPerPage
  const currentOrders = commandes.slice(indexOfFirstOrder, indexOfLastOrder)
  const paginate = (pageNumber) => setCurrentPage(pageNumber)

  const getStatusIcon = (state) => {
    switch (state) {
      case "to_delivered":
        return <Truck className="w-5 h-5 text-blue-500" />
      case "delivered":
      case "sale":
        return <CheckCircle className="w-5 h-5 text-green-500" />
      default:
        return <Clock className="w-5 h-5 text-yellow-500" />
    }
  }

  const getStatusText = (state) => {
    switch (state) {
      case "to_delivered":
        return "En cours de livraison"
      case "delivered":
        return "Livré"
      case "sale":
        return "Validé"
      default:
        return "En attente"
    }
  }

  const toggleRow = (commandeId) => {
    setExpandedRows((prev) => {
      const newSet = new Set(prev)
      if (newSet.has(commandeId)) {
        newSet.delete(commandeId)
      } else {
        newSet.add(commandeId)
      }
      return newSet
    })
  }

  return (
    <div className="bg-white shadow-md rounded-lg p-6">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold">Commandes Vente en cher</h2>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900"></div>
        </div>
      ) : commandes.length === 0 ? (
        <p className="text-center text-gray-500">Aucune commande Vente en cher pour le moment</p>
      ) : (
        <>
          <div className="overflow-x-auto">
            <table className="min-w-full bg-white">
              <thead className="bg-gray-100">
                <tr>
                  <th className="py-2 px-4 text-left w-8"></th>
                  <th className="py-2 px-4 text-left">Commande</th>
                  <th className="py-2 px-4 text-left">Date</th>
                  <th className="py-2 px-4 text-left">Statut</th>
                  <th className="py-2 px-4 text-left">Montant</th>
                </tr>
              </thead>
              <tbody>
                {currentOrders.map((commande) => {
                  const isExpanded = expandedRows.has(commande.id)
                  const orderLines = Array.isArray(commande.order_lines) ? commande.order_lines : []
                  return (
                    <>
                      <tr key={commande.id} className="border-b hover:bg-gray-50">
                        <td className="py-2 px-4">
                          {orderLines.length > 0 && (
                            <button
                              onClick={() => toggleRow(commande.id)}
                              className="p-1 hover:bg-gray-200 rounded transition-colors"
                              aria-label={isExpanded ? "Réduire" : "Développer"}
                            >
                              {isExpanded ? (
                                <ChevronUp className="w-4 h-4" />
                              ) : (
                                <ChevronDown className="w-4 h-4" />
                              )}
                            </button>
                          )}
                        </td>
                        <td className="py-2 px-4">#{commande.name || commande.id}</td>
                        <td className="py-2 px-4">
                          {commande.date_order ? formatDate(commande.date_order) : "-"}
                        </td>
                        <td className="py-2 px-4">
                          <div className="flex items-center">
                            {getStatusIcon(commande.state)}
                            <span className="ml-2">{getStatusText(commande.state)}</span>
                          </div>
                        </td>
                        <td className="py-2 px-4 font-semibold text-qblack">
                          {formatPrice(commande.amount_total || 0)}
                        </td>
                      </tr>
                      {isExpanded && orderLines.length > 0 && (
                        <tr>
                          <td colSpan="5" className="py-4 px-4 bg-gray-50">
                            <div className="pl-4">
                              <h4 className="font-semibold text-sm mb-3 text-gray-700">
                                Produits de la commande :
                              </h4>
                              <div className="overflow-x-auto">
                                <table className="min-w-full text-sm">
                                  <thead className="bg-gray-200">
                                    <tr>
                                      <th className="py-2 px-3 text-left text-xs font-semibold text-gray-700">
                                        Produit
                                      </th>
                                      <th className="py-2 px-3 text-left text-xs font-semibold text-gray-700">
                                        Quantité
                                      </th>
                                      <th className="py-2 px-3 text-left text-xs font-semibold text-gray-700">
                                        Prix unitaire
                                      </th>
                                      <th className="py-2 px-3 text-left text-xs font-semibold text-gray-700">
                                        Total
                                      </th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {orderLines.map((line, index) => (
                                      <tr key={index} className="border-b border-gray-200">
                                        <td className="py-2 px-3">
                                          {line.product_name || `Produit #${line.id || index + 1}`}
                                        </td>
                                        <td className="py-2 px-3">{line.quantity || 0}</td>
                                        <td className="py-2 px-3">
                                          {formatPrice(line.list_price || 0)}
                                        </td>
                                        <td className="py-2 px-3 font-semibold">
                                          {formatPrice((line.list_price || 0) * (line.quantity || 0))}
                                        </td>
                                      </tr>
                                    ))}
                                  </tbody>
                                </table>
                              </div>
                            </div>
                          </td>
                        </tr>
                      )}
                    </>
                  )
                })}
              </tbody>
            </table>
          </div>

          {commandes.length > ordersPerPage && (
            <div className="mt-6 flex justify-center space-x-2">
              {Array.from({ length: Math.ceil(commandes.length / ordersPerPage) }).map((_, index) => (
                <button
                  key={index}
                  onClick={() => paginate(index + 1)}
                  className={`px-3 py-1 rounded text-sm ${
                    currentPage === index + 1
                      ? "bg-blue-500 text-white"
                      : "bg-gray-200 text-gray-700 hover:bg-gray-300"
                  }`}
                >
                  {index + 1}
                </button>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  )
}

