import { useEffect, useRef, useState } from "react";
import { Pencil } from "lucide-react";
import { toast } from "react-toastify";
import userService from "../../../../services/userService";
import { useNavigate } from "react-router-dom";
import { useAuthContext } from "../../../../contexts/useAuthContext";

export default function ProfileTab() {
  const { user, parent } = useAuthContext();
  const navigate = useNavigate();
  const profileImgInput = useRef(null);
  const [profileImg, setProfileImg] = useState(null);

  // Fonction pour vérifier si une valeur est valide (non vide, non null, non undefined)
  const hasValue = (value) => {
    if (value === null || value === undefined) return false;
    if (typeof value === "boolean") return true; // Les booléens sont toujours valides
    if (typeof value === "number") return value !== 0; // 0 n'est pas considéré comme une valeur valide
    if (typeof value === "string") return value.trim() !== "";
    return true;
  };

  // Fonction pour formater la valeur d'affichage
  const formatValue = (value) => {
    if (typeof value === "boolean") return value ? "Oui" : "Non";
    if (value instanceof Date) return value.toLocaleDateString("fr-FR");
    return value;
  };

  // Fonction pour obtenir une valeur avec fallback
  const getValue = (key) => {
    return user?.[key] ?? parent?.[key];
  };

  // Fonction pour obtenir le téléphone (telephone en priorité, sinon partner_phone)
  const getTelephone = () => {
    const tel = getValue("telephone");
    if (hasValue(tel)) return { value: tel, label: "Téléphone" };
    const partnerTel = getValue("partner_phone");
    if (hasValue(partnerTel)) return { value: partnerTel, label: "Téléphone" };
    return null;
  };

  // Fonction pour obtenir le nom (prenom + nom en priorité, sinon name)
  const getNomFields = () => {
    const prenom = getValue("prenom");
    const nom = getValue("nom");
    if (hasValue(prenom) && hasValue(nom)) {
      return [
        { value: prenom, label: "Prénom", key: "prenom" },
        { value: nom, label: "Nom", key: "nom" },
      ];
    }
    const name = getValue("name");
    if (hasValue(name)) {
      return [{ value: name, label: "Nom complet", key: "name" }];
    }
    return [];
  };

  // Configuration des champs à afficher avec leurs labels
  const fieldConfig = {
    // Informations de base
    identite: {
      title: "Identité",
      fields: [
        { key: "email", label: "Email" },
        { key: "date_naissance", label: "Date de naissance" },
        { key: "lieu_naissance", label: "Lieu de naissance" },
        { key: "nationalite", label: "Nationalité" },
        { key: "cni_number", label: "Numéro CNI" },
      ],
    },
    contact: {
      title: "Contact",
      fields: [
        { key: "adresse", label: "Adresse" },
        { key: "partner_city", label: "Ville" },
        { key: "country_name", label: "Pays" },
        { key: "country_code", label: "Code pays" },
        { key: "is_verified", label: "Compte vérifié" },
      ],
    },
    professionnel: {
      title: "Informations Professionnelles",
      fields: [
        { key: "profession", label: "Profession" },
        // { key: "marge", label: "Marge", format: (v) => `${(v * 100).toFixed(1)}%` },
        { key: "rib", label: "RIB" },
        { key: "employer_partner_name", label: "Employeur" },
        { key: "employer_partner_email", label: "Email employeur" },
        { key: "employer_partner_phone", label: "Téléphone employeur" },
        { key: "employer_partner_adresse", label: "Adresse employeur" },
      ],
    },
  };

  // Fonction pour rendre une section de champs
  const renderFieldSection = (section) => {
    const validFields = section.fields.filter((field) => {
      const value = getValue(field.key);
      return hasValue(value);
    });

    if (validFields.length === 0) return null;

    return (
      <div className="mb-6 sm:mb-7 md:mb-8" key={section.title}>
        <h3 className="text-lg sm:text-xl font-semibold text-gray-800 mb-3 sm:mb-4 pb-2 border-b border-gray-200">
          {section.title}
        </h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
          {validFields.map((field) => {
            const value = getValue(field.key);
            const displayValue = field.format ? field.format(value) : formatValue(value);
            return (
              <div key={field.key} className="bg-gray-50 p-3 sm:p-4 rounded-lg">
                <div className="text-xs sm:text-sm font-medium text-gray-600 mb-1">
                  {field.label}
                </div>
                <div className="text-sm sm:text-base text-gray-900 font-semibold break-words">
                  {displayValue}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    );
  };

  useEffect(() => {
    if (!user) {
      navigate("/login");
      return;
    }

    const name = "avatar_" + user.id;
    const storedAvatar = localStorage.getItem(name);
    if (user.avatar) {
      setProfileImg(user.avatar);
    } else if (storedAvatar) {
      setProfileImg(storedAvatar);
    } else {
      setProfileImg("/images/edit-profileimg.jpg");
    }
  }, [user, navigate]);

  const profileImgChangeHandler = (e) => {
    const file = e.target.files[0];
    const name = "avatar_" + user.id;
    if (file) {
      const imgReader = new FileReader();
      imgReader.onload = async (event) => {
        setProfileImg(event.target.result);
        localStorage.setItem(name, event.target.result);
        try {
          const response = await userService.updateUserAvatar(event.target.result);
          if (response.status === "success") {
            toast.success(response.message, {
              position: "top-center",
              autoClose: 5000,
            });
            setProfileImg(response.avatar);
          }
        } catch (error) {
          toast.error("Erreur lors de la mise à jour de l'avatar");
        }
      };
      imgReader.readAsDataURL(file);
    } else {
      setProfileImg("/images/edit-profileimg.jpg");
      localStorage.removeItem(name);
    }
  };

  const browseProfileImg = () => {
    profileImgInput.current?.click();
  };

  if (!user) {
    return null;
  }

  return (
    <div className="container mx-auto px-3 sm:px-4 md:px-6 py-4 sm:py-6 md:py-8">
      <div className="flex flex-col lg:flex-row space-y-4 sm:space-y-6 md:space-y-8 lg:space-y-0 lg:space-x-6 xl:space-x-8">
        {/* Section de l'image de profil */}
        <div className="w-full lg:w-1/3 xl:w-1/4">
          <div className="bg-white rounded-lg shadow-md p-4 sm:p-5 md:p-6">
            <h1 className="text-lg sm:text-xl md:text-2xl font-bold text-gray-800 flex items-center mb-4 sm:mb-5 md:mb-6">
              <span className="hidden sm:inline">Mettre à jour le profil</span>
              <span className="sm:hidden">Profil</span>
              <span className="ml-2">
                <svg
                  className="w-5 h-5 sm:w-6 sm:h-6"
                  viewBox="0 0 20 20"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M10 0C4.47457 0 0 4.47791 0 10C0 15.5221 4.47791 20 10 20C15.5221 20 20 15.5221 20 10C19.9967 4.48126 15.5221 0.00669344 10 0ZM10 16.67C9.53815 16.67 9.16667 16.2985 9.16667 15.8367C9.16667 15.3748 9.53815 15.0033 10 15.0033C10.4618 15.0033 10.8333 15.3748 10.8333 15.8367C10.8333 16.2952 10.4618 16.67 10 16.67ZM11.6098 10.425C11.1078 10.7396 10.8132 11.2952 10.8333 11.8842V12.5033C10.8333 12.9652 10.4618 13.3367 10 13.3367C9.53815 13.3367 9.16667 12.9652 9.16667 12.5033V11.8842C9.14324 10.6861 9.76907 9.56827 10.8032 8.96586C11.4357 8.61781 11.7704 7.90161 11.6366 7.19545C11.5027 6.52276 10.9772 5.99732 10.3046 5.8668C9.40094 5.69946 8.5308 6.29853 8.36346 7.20214C8.34673 7.30254 8.33668 7.40295 8.33668 7.50335C8.33668 7.96519 7.9652 8.33668 7.50335 8.33668C7.0415 8.33668 6.67002 7.96519 6.67002 7.50335C6.67002 5.66265 8.16265 4.17001 10.0067 4.17001C11.8474 4.17001 13.34 5.66265 13.34 7.50669C13.3333 8.71821 12.674 9.83601 11.6098 10.425Z"
                    fill="#374557"
                    fillOpacity="0.6"
                  />
                </svg>
              </span>
            </h1>

            <div className="flex justify-center">
              <div className="relative">
                <div className="w-32 h-32 sm:w-40 sm:h-40 md:w-48 md:h-48 rounded-full overflow-hidden relative mx-auto">
                  <img
                    src={profileImg || "/images/edit-profileimg.jpg"}
                    alt="CCBM Shop Profile"
                    className="object-cover w-full h-full"
                  />
                </div>
                <input
                  ref={profileImgInput}
                  onChange={profileImgChangeHandler}
                  type="file"
                  className="hidden"
                  accept="image/*"
                />
                <button
                  onClick={browseProfileImg}
                  className="absolute bottom-1 right-1 sm:bottom-2 sm:right-2 w-8 h-8 sm:w-10 sm:h-10 bg-blue-500 rounded-full flex items-center justify-center cursor-pointer hover:bg-blue-600 transition-colors shadow-lg"
                  aria-label="Modifier l'avatar"
                >
                  <Pencil className="text-white w-4 h-4 sm:w-5 sm:h-5" />
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Section des informations personnelles */}
        <div className="w-full lg:w-2/3 xl:w-3/4">
          <div className="bg-white rounded-lg shadow-md p-4 sm:p-5 md:p-6">
            <h2 className="text-xl sm:text-2xl font-bold mb-4 sm:mb-5 md:mb-6 text-gray-800">
              Informations Personnelles
            </h2>
            
            {/* Section Identité avec gestion spéciale du nom */}
            <div className="mb-6 sm:mb-7 md:mb-8">
              <h3 className="text-lg sm:text-xl font-semibold text-gray-800 mb-3 sm:mb-4 pb-2 border-b border-gray-200">
                Identité
              </h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
                {getNomFields().map((field) => (
                  <div key={field.key} className="bg-gray-50 p-3 sm:p-4 rounded-lg">
                    <div className="text-xs sm:text-sm font-medium text-gray-600 mb-1">
                      {field.label}
                    </div>
                    <div className="text-sm sm:text-base text-gray-900 font-semibold break-words">
                      {formatValue(field.value)}
                    </div>
                  </div>
                ))}
                {fieldConfig.identite.fields.map((field) => {
                  const value = getValue(field.key);
                  if (!hasValue(value)) return null;
                  const displayValue = field.format ? field.format(value) : formatValue(value);
                  return (
                    <div key={field.key} className="bg-gray-50 p-3 sm:p-4 rounded-lg">
                      <div className="text-xs sm:text-sm font-medium text-gray-600 mb-1">
                        {field.label}
                      </div>
                      <div className="text-sm sm:text-base text-gray-900 font-semibold break-words">
                        {displayValue}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Section Contact avec gestion spéciale du téléphone */}
            <div className="mb-6 sm:mb-7 md:mb-8">
              <h3 className="text-lg sm:text-xl font-semibold text-gray-800 mb-3 sm:mb-4 pb-2 border-b border-gray-200">
                Contact
              </h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
                {(() => {
                  const telField = getTelephone();
                  if (telField) {
                    return (
                      <div key="telephone" className="bg-gray-50 p-3 sm:p-4 rounded-lg">
                        <div className="text-xs sm:text-sm font-medium text-gray-600 mb-1">
                          {telField.label}
                        </div>
                        <div className="text-sm sm:text-base text-gray-900 font-semibold break-words">
                          {formatValue(telField.value)}
                        </div>
                      </div>
                    );
                  }
                  return null;
                })()}
                {fieldConfig.contact.fields.map((field) => {
                  const value = getValue(field.key);
                  if (!hasValue(value)) return null;
                  const displayValue = field.format ? field.format(value) : formatValue(value);
                  return (
                    <div key={field.key} className="bg-gray-50 p-3 sm:p-4 rounded-lg">
                      <div className="text-xs sm:text-sm font-medium text-gray-600 mb-1">
                        {field.label}
                      </div>
                      <div className="text-sm sm:text-base text-gray-900 font-semibold break-words">
                        {displayValue}
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>

            {/* Section Professionnel */}
            {renderFieldSection(fieldConfig.professionnel)}
          </div>
        </div>
      </div>
    </div>
  );
}

