/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
import { use, useContext, useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";

import BreadcrumbCom from "../../BreadcrumbCom";
import Layout from "../../Partials/Layout";

import IcoCart from "./icons/IcoCart";
import IcoDashboard from "./icons/IcoDashboard";
import IcoLogout from "./icons/IcoLogout";
import IcoLove from "./icons/IcoLove";

import IcoPeople from "./icons/IcoPeople";

import AddressesTab from "./tabs/AddressesTab";
import Dashboard from "./tabs/Dashboard";
import OrderTab from "./tabs/OrderTab";
import PreOrderTab from "./tabs/PreOrderTab";
import PasswordTab from "./tabs/PasswordTab";
import Payment from "./tabs/Payment";
import ProfileTab from "./tabs/ProfileTab";
import SupportTab from "./tabs/SupportTab";
import WishlistTab from "./tabs/WishlistTab";

import PanierCommande from "./tabs/PanierCommande";
import PanierPreCommande from "./tabs/PanierPreCommande";
import PaymentDetailsTab from "./tabs/PaymentDetailsTab";
import OrderDraftTab from "./tabs/OrderDraftTab";
import CommandesACredit from "./tabs/CommandesACredit";
import CommandesVenteCher from "./tabs/CommandesVenteCher";
import { useAuthContext } from "../../../contexts/useAuthContext";

export default function Profile() {


  const { user, logout } = useAuthContext();

  const navigate = useNavigate();
  const location = useLocation();
  const getHashContent = location.hash.split("#");
  const [active, setActive] = useState("dashboard");
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  

  useEffect(() => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
    setActive(
      getHashContent && getHashContent.length > 1
        ? getHashContent[1]
        : "dashboard"
    );

  }, [getHashContent]);

  useEffect(() => {
    if (active != "") {
      setIsMenuOpen(false);
    }
  }, [active]);


  function HandleLout() {
    logout();
    navigate("/login");
  }
  return (
    <Layout childrenClasses="pt-0 pb-0">
      <div className="profile-page-wrapper w-full">
        <div className="container-x mx-auto px-3 sm:px-4 md:px-6">
          <div className="w-full my-4 sm:my-6 md:my-8 lg:my-10">
            <BreadcrumbCom
              paths={[
                { name: "Accueil", path: "/" },
                { name: "profile", path: "/profile" },
              ]}
            />

            <div className="w-full bg-white px-3 sm:px-6 md:px-8 lg:px-10 py-4 sm:py-6 md:py-8 lg:py-9 rounded-lg shadow-sm">
              <div className="title-area w-full flex justify-between items-center mb-4 sm:mb-6 md:mb-8">
                <h1 className="text-lg sm:text-xl md:text-[22px] font-bold text-qblack">
                  Tableau de bord
                </h1>
                <button
                  className="block lg:hidden text-qblack p-2 hover:bg-gray-100 rounded-md transition-colors"
                  onClick={toggleMenu}
                  aria-label="Toggle menu"
                >
                  {isMenuOpen ? (
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth={2}
                      stroke="currentColor"
                      className="w-6 h-6 sm:w-7 sm:h-7"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M6 18L18 6M6 6l12 12"
                      />
                    </svg>
                  ) : (
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth={2}
                      stroke="currentColor"
                      className="w-6 h-6 sm:w-7 sm:h-7"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M4 6h16M4 12h16m-7 6h7"
                      />
                    </svg>
                  )}
                </button>
              </div>

              <div className="profile-wrapper w-full flex flex-col lg:flex-row lg:space-x-6 xl:space-x-10">
                <div
                  className={`w-full lg:w-[236px] xl:w-[280px] lg:min-h-[600px] border-b lg:border-b-0 lg:border-r border-[rgba(0, 0, 0, 0.1)] pb-6 lg:pb-0 pr-0 lg:pr-6 xl:pr-10 ${
                    isMenuOpen ? "block" : "hidden"
                  } lg:block transition-all duration-300 ease-in-out`}
                >
                  <div className="flex flex-col space-y-4 sm:space-y-6 md:space-y-8 lg:space-y-10">
                    {/* Menu items */}
                    <MenuItem
                      to="/profile#dashboard"
                      icon={<IcoDashboard />}
                      text="Tableau de bord"
                      onClick={() => setActive("dashboard")}
                    />
                    <MenuItem
                      to="/profile#profile"
                      icon={<IcoPeople />}
                      text="Information Personnel"
                      onClick={() => setActive("profile")}
                    />
                    <MenuItem
                      to="/profile#order"
                      icon={<IcoCart />}
                      text="Commandes"
                      onClick={() => setActive("order")}
                    />
                    {user && (
                      <MenuItem
                        to="/profile#commandes-vente-cher"
                        icon={<IcoCart />}
                        text="Commandes Vente en cher"
                        onClick={() => setActive("commandes-vente-cher")}
                      />
                    )}
                    {user && (
                      <MenuItem
                        to="/profile#commandes-a-credit"
                        icon={<IcoCart />}
                        text="Commandes à crédit"
                        onClick={() => setActive("commandes-a-credit")}
                      />
                    )}
                    <MenuItem
                      onClick={HandleLout}
                      icon={<IcoLogout />}
                      text="Déconnexion"
                    />
                  </div>
                </div>

                <div className={`flex-1 ${isMenuOpen ? 'hidden' : 'block'} lg:block mt-4 lg:mt-0`}>
                  <div className="item-body dashboard-wrapper w-full">
                    {active === "dashboard" && <Dashboard />}
                    {active === "profile" && <ProfileTab />}
                    {active === "payment" && <Payment />}
                    {active === "order" && <OrderTab />}
                    {active === "orderDraft" && <OrderDraftTab />}
                    {active === "payments" && <PaymentDetailsTab />}
                    {active === "preorder" && <PreOrderTab />}
                    {active === "wishlist" && <WishlistTab />}
                    {active === "paniercommande" && <PanierCommande />}
                    {user && active === "commandes-a-credit" && <CommandesACredit />}
                    {user && active === "commandes-vente-cher" && <CommandesVenteCher />}
                    {active === "panierprecommande" && <PanierPreCommande />}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
}

const MenuItem = ({ to, icon, text, onClick }) => (
  <div className="item group">
    {to ? (
      <Link to={to} onClick={onClick}>
        <div className="flex space-x-2 sm:space-x-3 items-center text-qgray hover:text-qblack transition-colors py-2 px-2 rounded-md hover:bg-gray-50">
          <span className="flex-shrink-0">{icon}</span>
          <span className="font-normal text-sm sm:text-base">{text}</span>
        </div>
      </Link>
    ) : (
      <button
        onClick={onClick}
        className="w-full text-left"
      >
        <div className="flex space-x-2 sm:space-x-3 items-center text-qgray hover:text-qblack transition-colors py-2 px-2 rounded-md hover:bg-gray-50">
          <span className="flex-shrink-0">{icon}</span>
          <span className="font-normal text-sm sm:text-base">{text}</span>
        </div>
      </button>
    )}
  </div>
);