
/* eslint-disable react/prop-types */
import { useEffect, useState } from "react";
import formatDate from "../../../../utils/date-format";
import formatPrice from "../../../../utils/formatPrice";
import { useAuthContext } from "../../../../contexts/useAuthContext";
import { CheckCircle, XCircle, Eye, ChevronLeft, ChevronRight } from "lucide-react";


import MobileMoneyService from "../../../../services/Payment/mobile-money-service";

/**
 * Normalisation des transactions en un format commun
 */
function normalizeOrange(t) {
  return {
    source: "OM",
    transaction_id: t.transaction_id,
    order_id: (t.order && t.order.id),
    order_name: (t.order && t.order.name),
    order_type: (t.order && t.order.type_sale) || "order",
    amount: Number(t.amount) || 0,
    currency: t.currency || "XOF",
    phone: t.customer_msisdn || "",
    description: t.description || "",
    url_facture: t.url_facture || "",
    status_raw: String(t.status || "").toUpperCase(), // SUCCESS | FAILED | PENDING ...
    // date d’affichage : completed_at > updated_at > created_at
    payment_date: t.completed_at || t.updated_at || t.created_at || null,
  };
}

function normalizeWave(t) {
  return {
    source: "WAVE",
    transaction_id: t.transaction_id,
    order_id: t.order?.id,
    order_name: t.order?.name,
    order_type: (t.order && t.order.type_sale) || t.order_type || "order",
    amount: Number(t.amount) || 0,
    currency: t.currency || "XOF",
    phone: t.phone || "",
    description: t.description || "",
    url_facture: t.url_facture || "",
    // completed / failed / pending ... (checkout_status/payment_status inclus côté API)
    status_raw: String(t.status || "").toUpperCase(),
    payment_date: t.completed_at || t.created_at || null,
  };
}

function toUiState(statusRaw) {
  const s = String(statusRaw).toUpperCase();
  if (["SUCCESS", "SUCCEEDED", "COMPLETED", "PAID", "SALE"].includes(s)) return "paid";
  if (["FAILED", "CANCELLED", "CANCELED", "EXPIRED", "REJECTED"].includes(s)) return "failed";
  return "pending";
}

export default function PaymentDetailsTab() {
  const [payments, setPayments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const [paymentsPerPage] = useState(5);
  const [showModal, setShowModal] = useState(false);
  const [selectedPayment, setSelectedPayment] = useState(null);
  const { user } = useAuthContext();

  // --- Chargement combiné OM + Wave ---
  useEffect(() => {
    let mounted = true;
    async function load() {
      setLoading(true);
      try {
        // 1) Appels parallèles vers tes routes backend
        const [omList, waveList] = await Promise.all([
          MobileMoneyService.getOrangeTransactionsByPartner(user.partner_id), // GET /api/payment/orange/partner/<id>/transactions
          MobileMoneyService.getWaveTransactionsByPartner(user.partner_id),   // GET /api/payment/wave/partner-transactions/<id>
        ]);
        console.log(omList, waveList)
        // 2) Normalise & fusionne
        const all = [
          ...(Array.isArray(omList) ? omList.map(normalizeOrange) : []),
          ...(Array.isArray(waveList) ? waveList.map(normalizeWave) : []),
        ];

        // 3) Trie par date (desc)
        all.sort((a, b) => {
          const da = a.payment_date ? new Date(a.payment_date).getTime() : 0;
          const db = b.payment_date ? new Date(b.payment_date).getTime() : 0;
          return db - da;
        });

        if (mounted) setPayments(all);
      } catch (err) {
        console.error("Erreur chargement paiements:", err);
        if (mounted) setPayments([]);
      } finally {
        if (mounted) setLoading(false);
      }
    }
    load();
    return () => { mounted = false; };
  }, [user.partner_id]);

  const handleDetails = (e, payment) => {
    e.preventDefault();
    setSelectedPayment(payment);
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
    setSelectedPayment(null);
  };

  const handleOpenInvoice = (e, facture) => {
    e.preventDefault();
    if (facture) window.open(facture, "_blank", "noopener,noreferrer");
  };

  // Pagination
  const indexOfLastPayment = currentPage * paymentsPerPage;
  const indexOfFirstPayment = indexOfLastPayment - paymentsPerPage;
  const currentPayments = payments.slice(indexOfFirstPayment, indexOfLastPayment);
  const totalPages = Math.ceil(payments.length / paymentsPerPage);
  const paginate = (n) => setCurrentPage(n);

  return (
    <div className="bg-white shadow-md rounded-lg p-6">
      <div className="flex items-center mb-6">
        <h2 className="text-2xl font-bold">Détails des paiements</h2>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-gray-900"></div>
        </div>
      ) : payments.length === 0 ? (
        <p className="text-center text-gray-500">Aucun paiement effectué</p>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead className="bg-gray-100">
              <tr>
                <th className="py-2 px-4 text-left">Passerelle</th>
                <th className="py-2 px-4 text-left">Commande</th>
                <th className="py-2 px-4 text-left">Date</th>
                <th className="py-2 px-4 text-left">Statut</th>
                <th className="py-2 px-4 text-left">Montant</th>
                <th className="py-2 px-4 text-left">Action</th>
              </tr>
            </thead>
            <tbody>
              {currentPayments.map((pay, idx) => {
                const state = toUiState(pay.status_raw);
                const isPending = state === "pending";
                const isPaid = state === "paid";
                return (
                  <tr key={`${pay.source}-${pay.transaction_id}-${idx}`} className="border-b">
                    <td className="py-2 px-4">{pay.source}</td>
                    <td className="py-2 px-4">#{pay.order_name}</td>
                    <td className="py-2 px-4">{pay.payment_date ? formatDate(pay.payment_date) : "-"}</td>
                    <td className="py-2 px-4">
                      <div className="flex items-center">
                        {isPaid ? (
                          <CheckCircle className="w-5 h-5 text-green-500 mr-2" />
                        ) : isPending ? (
                          <XCircle className="w-5 h-5 text-yellow-500 mr-2" />
                        ) : (
                          <XCircle className="w-5 h-5 text-red-500 mr-2" />
                        )}
                        <span
                          className={
                            isPaid
                              ? "text-green-600"
                              : isPending
                                ? "text-yellow-600"
                                : "text-red-600"
                          }
                        >
                          {isPaid ? "Payé" : isPending ? "En attente" : "Échoué"}
                        </span>
                      </div>
                    </td>
                    <td className="py-2 px-4">
                      <span className={isPaid ? "text-green-600" : isPending ? "text-yellow-600" : "text-red-600"}>
                        {formatPrice(pay.amount)}
                      </span>
                    </td>
                    <td className="py-2 px-4">
                      <button
                        onClick={(e) => handleDetails(e, pay)}
                        className="flex items-center px-3 py-1 bg-yellow-500 text-white rounded hover:bg-yellow-600 transition-colors"
                      >
                        <Eye className="w-4 h-4 mr-2" />
                        Détails
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      )}

      {!loading && payments.length > paymentsPerPage && (
        <div className="mt-6 flex justify-center">
          <nav className="relative z-0 inline-flex rounded-md shadow-sm -space-x-px" aria-label="Pagination">
            <button
              onClick={() => paginate(currentPage - 1)}
              disabled={currentPage === 1}
              className="relative inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50 disabled:opacity-50"
            >
              <span className="sr-only">Previous</span>
              <ChevronLeft className="h-5 w-5" aria-hidden="true" />
            </button>
            {Array.from({ length: totalPages }).map((_, index) => (
              <button
                key={index}
                onClick={() => paginate(index + 1)}
                className={`relative inline-flex items-center px-4 py-2 border text-sm font-medium ${currentPage === index + 1
                    ? "z-10 bg-blue-50 border-blue-500 text-blue-600"
                    : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                  }`}
              >
                {index + 1}
              </button>
            ))}
            <button
              onClick={() => paginate(currentPage + 1)}
              disabled={currentPage === totalPages}
              className="relative inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-50 disabled:opacity-50"
            >
              <span className="sr-only">Next</span>
              <ChevronRight className="h-5 w-5" aria-hidden="true" />
            </button>
          </nav>
        </div>
      )}

      {/* Modal */}
      {showModal && selectedPayment && (
        <div className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
          <div className="bg-white p-6 rounded-lg w-full max-w-md">
            <h2 className="text-xl font-bold mb-4">Détails du paiement</h2>
            <div className="mb-4">
              <p className="text-gray-600">
                <strong>Passerelle :</strong> {selectedPayment.source}
              </p>
              <p className="text-gray-600">
                <strong>Transaction ID :</strong> {selectedPayment.transaction_id}
              </p>
              <p className="text-gray-600">
                <strong>Date :</strong>{" "}
                {selectedPayment.payment_date ? formatDate(selectedPayment.payment_date) : "-"}
              </p>
              <p className="text-gray-600">
                <strong>Statut :</strong>{" "}
                {toUiState(selectedPayment.status_raw) === "paid"
                  ? "Payé"
                  : toUiState(selectedPayment.status_raw) === "pending"
                    ? "En attente"
                    : "Échoué"}
              </p>
              <p className="text-gray-600">
                <strong>Nom {selectedPayment.order_type === "order" ? "Commande" : "Précommande"} :</strong>{" "}
                {selectedPayment.order_name}
              </p>
              <p className="text-gray-600">
                <strong>Montant payé :</strong> {formatPrice(selectedPayment.amount)}
              </p>
              <p className="text-gray-600">
                <strong>Téléphone :</strong> {selectedPayment.phone || "-"}
              </p>
              <p className="text-gray-600">
                <strong>Description :</strong> {selectedPayment.description || "-"}
              </p>
              <p className="text-gray-600">
                <strong>Facture :</strong>{" "}
                {selectedPayment.url_facture ? (
                  <button
                    className="text-blue-500 underline"
                    onClick={(e) => handleOpenInvoice(e, selectedPayment.url_facture)}
                  >
                    Ouvrir la facture
                  </button>
                ) : (
                  <span>-</span>
                )}
              </p>
            </div>
            <button
              onClick={closeModal}
              className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600 transition-colors"
            >
              Fermer
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
